
import { formateDate } from '../utils/validate'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import cookie from 'vux/src/tools/cookie/index.js'

import { mapState } from 'vuex'
import api from '../api'
export default {
name: 'index',
data () {
return {
}
},
components: {
Grid,
GridItem
},
created () {
this.$store.dispatch({type: 'getColumns', url: api.getColumns})
this.$store.dispatch({type: 'getLists', url: api.getLists(0)})
},
computed: {
...mapState([
'msg',
'columns',
'lists'
])
},
methods: {
onItemClick (val) {
cookie.set('sub', JSON.stringify(val))
}
},
filters: {
parseTime (val) {
return formateDate(val)
}
}
}



